import React from 'react'
import './style.css'
import { instancestore } from '../../../store/InstanceStore';
import { useColourObjectStore } from '../../../store/ColourStore'


const Spinner = () => {

  const { colourObj } = useColourObjectStore();

  const { activeInstanceData } = instancestore();

  return (
    <div  className="maian_container">
      <div className='loader_logoicon_containers'>
        <img className='loader_logoicon' src={activeInstanceData?.i_icon} />
        <div className="loader" style={{ borderColor: colourObj?.color_m2 }}>
        </div>
      </div>

      <div className="poweredby_text">
        Powered by Media Cannibal.
      </div>

    </div>
  )
}

export default Spinner